code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

body {
	margin: 0;
	padding: 0;
	background-color: #212121;
	padding-top: 50px;
}

.Cell {
	height: 100px;
	width: 100px;
	border-radius: 4px;
	background-color: #263228;
	transition: background-color 0.7s ease;
}

.Cell-lit {
	background-color: #00bcd4;
}

.Board {
	margin: 0 auto;
	border-radius: 10px;
	box-shadow: 0 0 30px #fff, -10px -10px 20px #f40a35, 10px 10px 20px #426dfb;
}

/* Top left */
table tr:first-child td:first-child {
	border-top-left-radius: 10px !important;
}

/* Top right */
table tr:first-child td:last-child {
	border-top-right-radius: 10px !important;
}

/* Bottom right */
table tr:last-child td:last-child {
	border-bottom-right-radius: 10px !important;
}
/* Bottom left */
table tr:last-child td:first-child {
	border-bottom-left-radius: 10px !important;
}

@font-face {
	font-family: neon;
	src: url(https://s3-us-west-2.amazonaws.com/s.cdpn.io/707108/neon.ttf);
}

.Board-title-top {
	text-align: start;
}

.Board-title-bottom {
	align-items: flex-start;
	text-align: end;
}

.neon-orange {
	font-family: neon;
	color: #fb4264;
	font-size: 3rem;
	text-shadow: 0 0 3vw #f40a35;
	animation: neon-orange 1s ease infinite;
	-moz-animation: neon-orange 1s ease infinite;
	-webkit-animation: neon-orange 1s ease infinite;
}

.neon-blue {
	font-family: neon;
	color: #426dfb;
	font-size: 5rem;
	text-shadow: 0 0 3vw #2356ff;
	animation: neon-blue 2s linear infinite;
	-moz-animation: neon-blue 2s linear infinite;
	-webkit-animation: neon-blue 2s linear infinite;
	-o-animation: neon-blue 2s linear infinite;
}

@keyframes neon-orange {
	0%,
	100% {
		text-shadow: 0 0 1vw #fa1c16, 0 0 3vw #fa1c16, 0 0 10vw #fa1c16, 0 0 10vw #fa1c16, 0 0 .4vw #fed128,
			.5vw .5vw .1vw #806914;
		color: #fed128;
	}
	50% {
		text-shadow: 0 0 .5vw #800e0b, 0 0 1.5vw #800e0b, 0 0 5vw #800e0b, 0 0 5vw #800e0b, 0 0 .2vw #800e0b,
			.5vw .5vw .1vw #40340a;
		color: #806914;
	}
}

@keyframes neon-blue {
	0%,
	100% {
		text-shadow: 0 0 1vw #1041ff, 0 0 3vw #1041ff, 0 0 10vw #1041ff, 0 0 10vw #1041ff, 0 0 .4vw #8bfdfe,
			.5vw .5vw .1vw #147280;
		color: #28d7fe;
	}
	50% {
		text-shadow: 0 0 .5vw #082180, 0 0 1.5vw #082180, 0 0 5vw #082180, 0 0 5vw #082180, 0 0 .2vw #082180,
			.5vw .5vw .1vw #0a3940;
		color: #146c80;
	}
}

.App {
	display: flex;
	justify-content: center;
	align-items: center;
	/* height: 100vh; */
}

